.App {
  text-align: center;
  background-color: #D8D4D3;
  height: 1000px;
}

.App-logo {
  height: 45vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.new-app-link {
  color: orange;
}

.input{
  width: 200px;
  height: 30px;
  font-size: 20px;
}

.question{
  font-size: 30px;
}

.dropdown{
  cursor: pointer;
}

